import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import IssueReceiptUpdateForm from "./updateForm";
import IssueReceiptDocUpdateForm from "./updateDocForm";

import notification from "../../../../components/notification";
import { Spin, Tooltip, Tabs } from "antd";

// import theme from "../../../../settings/themes/themedefault";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/issueReceipt/actions";
import { Box } from "../../../App/app.style";

import HistoryIssueReceipt from "./history";
import Print from "./print";

const redirecTo = "/dashboard/inventories/issue-receipts";
const TabPane = Tabs.TabPane;

class UpdateIssueReceipt extends Component {
  state = {
    visible: false
  };
  componentDidMount() {
    const { editIssueReceipt } = this.props;
    const { id } = this.props.match.params;
    editIssueReceipt(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.issueReceipt.error) {
      notification("error", this.props.issueReceipt.error);
      this.props.clearError();
    }

    if (this.props.issueReceipt.success) {
      // this.props.history.goBack();
      notification("success", this.props.issueReceipt.success);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updateIssueReceipt } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing issueReceipt code
    // value.customer_type_code = value.customer_type_code.toUpperCase();
    // console.log(value);
    updateIssueReceipt(id, value);
  };

  handleSubmitConfirm = value => {
    const { confirmIssueReceipt } = this.props;
    const { id } = this.props.match.params;

    confirmIssueReceipt(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelIssueReceipt } = this.props;
    const { id } = this.props.match.params;

    cancelIssueReceipt(id, value);
  };

  render() {
    // console.log(this.props);
    const { loading, loadingSubmit, issueReceipt } = this.props.issueReceipt;

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="issueReceipts.update.title" />}>
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="issueReceipts.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={issueReceipt} />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryIssueReceipt
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={issueReceipt ? issueReceipt.description : null}
          />
          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              {loading && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <IssueReceiptUpdateForm
                  submit={this.handleSubmit}
                  issueReceipt={this.props.issueReceipt}
                  handleSubmitConfirm={this.handleSubmitConfirm}
                  handleSubmitCancel={this.handleSubmitCancel}
                />
              )}
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
            >
              {loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <IssueReceiptDocUpdateForm
                  submit={this.handleSubmit}
                  issueReceipt={this.props.issueReceipt}
                  params={this.props.match.params}
                />
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    issueReceipt: state.issueReceipt,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateIssueReceipt);
