import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  render() {
    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;

    const column = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        align: "center"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        render: text => {
          if (text && text.length > 25) {
            return text.substring(0, 25) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "oldQty" }),
        dataIndex: "old_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "adjustmentQty" }),
        dataIndex: "qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "newQty" }),
        dataIndex: "new_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "cost" }),
        dataIndex: "cost",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "subTotal" }),
        dataIndex: "sub_total",
        width: "10%",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const leftSide = [
      {
        label: <IntlMessages id="issueReceipts.location" />,
        description: data.location ? data.location.location_name : null
      },
      {
        label: <IntlMessages id="issueReceipts.costAdjustment" />,
        description: data.cost_adjustment ? (
          <IntlMessages id="yes" />
        ) : (
          <IntlMessages id="no" />
        )
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="issueReceipts.adjustmentType" />,
        description: data.adjustment_type
          ? data.adjustment_type.description
          : null
      },
      {
        label: <IntlMessages id="issueReceipts.adjustmentAccount" />,
        description: data.account
          ? `${data.account.account_code} ${data.account.account_name}`
          : null
      }
    ];

    const total = [
      {
        label: <IntlMessages id="totalItem" />,
        amount: <NumberFormat value={data.total_item} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="totalQuantity" />,
        amount: <NumberFormat value={data.total_qty} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="totalCost" />,
        amount: <NumberFormat value={data.total_amount} />
        // parseAmount: true
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.issueReceipt" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.issue_receipt_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                            data.transaction_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian atas (Subheader) */}
          <Row>
            <Col push={12} span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="note" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={13}>
                  <span>{data.note}</span>
                </Col>
              </Row>
            </Col>
          </Row>

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="purchaseInvoices.tabs.item"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={data.issue_receipt_details}
                rowKey={row => row.id_issue_receipt_detail}
                pagination={false}
              />
            </Col>
          </Row>

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col push={12} span={12}>
              <TotalField data={total} />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName(
              "issue_receipt",
              data.issue_receipt_no,
              true
            ))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
